  padding: 0 12px 65px 12px;
  min-height: 80vh;
  margin-top: 70px;
}

.left-side-menu {
  width: 240px;
  background: #ffffff;
  bottom: 0;
  padding: 20px 0;
  position: fixed;
  transition: all 0.1s ease-out;
  top: 70px;
  box-shadow: 0 0 35px 0 rgba(66, 72, 80, 0.15);
}
.left-side-menu .user-box .img-thumbnail {
  background-color: #ffffff;
  border-color: whitesmoke;
}

.user-box .user-name {
