}

#sidebar-menu > ul {
  list-style: none;
  padding: 0;
}
#sidebar-menu > ul > li > a {
  color: #6e768e;
  display: block;
  padding: 12px 20px;
  position: relative;
  transition: all 0.4s;
  font-family: "Karla", sans-serif;
  font-size: 0.95rem;
}
#sidebar-menu > ul > li > a:hover, #sidebar-menu > ul > li > a:focus, #sidebar-menu > ul > li > a:active {
  color: #71b6f9;
