}
#sidebar-menu > ul > li > a i {
  display: inline-block;
  line-height: 1.0625rem;
  margin: 0 7px 0 3px;
  text-align: center;
  vertical-align: middle;
  width: 16px;
  font-size: 1rem;
}
#sidebar-menu > ul > li > a svg {
  width: 16px;
  height: 16px;
  margin-left: 3px;
  margin-right: 10px;
}
