.error-text-box .text {
  fill: none;
  stroke-width: 6;
  stroke-linejoin: round;
  stroke-dasharray: 30 100;
  stroke-dashoffset: 0;
  animation: stroke 9s infinite linear;
}
.error-text-box .text:nth-child(5n+1) {
  stroke: #ff5b5b;
  animation-delay: -1.2s;
}
.error-text-box .text:nth-child(5n+2) {
  stroke: #f9c851;
  animation-delay: -2.4s;
}
.error-text-box .text:nth-child(5n+3) {
  stroke: #71b6f9;
  animation-delay: -3.6s;
}
.error-text-box .text:nth-child(5n+4) {
  stroke: #35b8e0;
  animation-delay: -4.8s;
}
.error-text-box .text:nth-child(5n+5) {
  stroke: #10c469;
  animation-delay: -6s;
}

@keyframes stroke {
  100% {
    stroke-dashoffset: -400;
  }
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .error-text-box .text {
    fill: #ff5b5b;
    stroke: #ff5b5b;
    stroke-width: 6;
    stroke-dasharray: 0 0;
    stroke-dashoffset: 0;
    animation: none;
