/**
 * Nano scroll stuff
*/
.nano {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.nano > .nano-content {
  position: absolute;
  overflow-x: hidden;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.nano > .nano-content:focus {
  outline: none;
}
.nano > .nano-content:-webkit-scrollbar {
  visibility: hidden;
}
.nano > .nano-pane {
  background: rgba(173, 181, 189, 0.1);
  position: absolute;
  width: 7px;
  right: 8px;
  top: 8px;
  bottom: 8px;
  visibility: hidden \9 ;
  /* Target only IE7 and IE8 with this hack */
  opacity: 0.01;
  border-radius: 5px;
  transition: 0.3s;
}
.nano > .nano-pane > .nano-slider {
  background: rgba(173, 181, 189, 0.35);
  position: relative;
  border-radius: 3px;
  transition: 0.3s;
  transition-property: background;
}
.nano > .nano-pane:hover .nano-slider, .nano > .nano-pane.active .nano-slider {
  background: #3a4250;
}

.has-scrollbar > .nano-content:-webkit-scrollbar {
  visibility: visible;
}

.nano:hover > .nano-pane,
.nano-pane.active,
.nano-pane.flashed {
  visibility: visible \9 ;
  /* Target only IE7 and IE8 with this hack */
  opacity: 0.99;
}

