//
// ion-rangeslider.scss
//

.irs {
    position: relative;
    display: block;
    -webkit-touch-callout: none;
    -ms-user-select: none;
    user-select: none;
    font-size: 12px;
}

.irs-line {
    position: relative;
    display: block;
    overflow: hidden;
    outline: none !important;
}

.irs-bar {
    position: absolute;
    display: block;
    width: 0;
}

.irs-shadow {
    position: absolute;
    display: none;
    width: 0;
}

.irs-handle {
    position: absolute;
    display: block;
    box-sizing: border-box;
    cursor: default;
    z-index: 1;

    &.type_last {
        z-index: 2;
    }
}

.irs-min,
.irs-max {
    position: absolute;
    display: block;
    cursor: default;
}

.irs-min {
    left: 0;
}

.irs-max {
    right: 0;
}

.irs-from,
.irs-to,
.irs-single {
    position: absolute;
    display: block;
    top: 0;
    cursor: default;
    white-space: nowrap;
}

.irs-grid {
    position: absolute;
    display: none;
    bottom: 0;
    width: 100%;
    height: 20px;
}

.irs-with-grid {
    .irs-grid {
        display: block;
    }
}

.irs-grid-pol {
    position: absolute;
    top: 0;
    left: 0;
    width: 1px;
    height: 8px;
    background: $secondary;

    &.small {
        height: 4px;
    }
}

.irs-grid-text {
    position: absolute;
    bottom: 0;
    /*rtl:ignore*/
    left: 0;
    white-space: nowrap;
    text-align: center;
    font-size: 9px;
    line-height: 9px;
    padding: 0 3px;
    color: $secondary;
}

.irs-disable-mask {
    position: absolute;
    display: block;
    top: 0;
    left: -1%;
    width: 102%;
    height: 100%;
    cursor: default;
    background: rgba(0, 0, 0, 0);
    z-index: 2;
}

.lt-ie9 .irs-disable-mask {
    background: $secondary;
    filter: alpha(opacity=0);
    cursor: not-allowed;
}

.irs-disabled {
    opacity: 0.4;
}

.irs-hidden-input {
    position: absolute !important;
    display: block !important;
    top: 0 !important;
    left: 0 !important;
    width: 0 !important;
    height: 0 !important;
    font-size: 0 !important;
    line-height: 0 !important;
    padding: 0 !important;
    margin: 0 !important;
    overflow: hidden;
    outline: none !important;
    z-index: -9999 !important;
    background: none !important;
    border-style: solid !important;
    border-color: transparent !important;
}

.irs--flat {
    height: 40px;

    &.irs-with-grid {
        height: 60px;
    }

    .irs-line {
        top: 25px;
        height: 12px;
        background-color: $progress-bg;
        border-radius: 4px;
    }

    .irs-bar {
        top: 25px;
        height: 12px;
        background-color: $primary;
    }

    .irs-bar--single {
        /*rtl:ignore*/
        border-radius: 4px 0 0 4px;
    }

    .irs-shadow {
        height: 1px;
        bottom: 16px;
        background-color: $progress-bg;
    }

    .irs-handle {
        top: 22px;
        width: 16px;
        height: 18px;
        background-color: transparent;
    }

    .irs-handle>i:first-child {
        position: absolute;
        display: block;
        top: 0;
        left: 50%;
        width: 2px;
        height: 100%;
        margin-left: -1px;
        background-color: darken($primary, 7%) !important;
    }

    .irs-min,
    .irs-max {
        top: 0;
        padding: 1px 3px;
        color: $gray-700;
        font-size: 10px;
        line-height: 1.333;
        text-shadow: none;
        background-color: $progress-bg;
        border-radius: 4px;
    }

    .irs-from,
    .irs-to,
    .irs-single {
        color: $white;
        font-size: 10px;
        line-height: 1.333;
        text-shadow: none;
        padding: 1px 5px;
        background-color: $primary;
        border-radius: 4px;
    }

    .irs-from:before,
    .irs-to:before,
    .irs-single:before {
        position: absolute;
        display: block;
        content: "";
        bottom: -6px;
        left: 50%;
        width: 0;
        height: 0;
        margin-left: -3px;
        overflow: hidden;
        border: 3px solid transparent;
        border-top-color: $primary;
    }

    .irs-grid-pol {
        background-color: $progress-bg;
    }

    .irs-grid-text {
        color: $gray-700;
    }
}

.irs--modern {
    .irs-line {
        border: none;
        background: $progress-bg;
    }

    .irs-bar {
        background: $primary;
        background: linear-gradient(to bottom, $primary 0%, darken($primary, 10%) 100%);
    }

    .irs-min,
    .irs-max {
        top: 0;
        padding: 1px 3px;
        color: $gray-700;
        font-size: 10px;
        line-height: 1.333;
        text-shadow: none;
        background-color: $progress-bg;
        border-radius: 4px;
    }
}

.irs--sharp {

    .irs-from,
    .irs-to,
    .irs-single,
    .irs-min,
    .irs-max,
    .irs-handle,
    .irs-bar {
        background-color: $primary;
    }

    .irs-line {
        background: $progress-bg;
    }

    .irs-from:before,
    .irs-to:before,
    .irs-single:before,
    .irs-handle>i:first-child {
        border-top-color: $primary;
    }

    .irs-handle.state_hover,
    .irs-handle:hover {
        background-color: darken($primary, 15%);
    }

    .irs-handle.state_hover>i:first-child,
    .irs-handle:hover>i:first-child {
        border-top-color: darken($primary, 15%);
    }
}

.irs--round {

    .irs-from,
    .irs-to,
    .irs-single,
    .irs-bar {
        background-color: $primary;

        &:before {
            border-top-color: $primary;
        }
    }

    .irs-handle {
        background-color: $progress-bg;
        border: 4px solid $primary;
        box-shadow: 0 1px 3px rgba($primary, 0.3);
    }

    .irs-min,
    .irs-max {
        color: $gray-700;
        background-color: $gray-300;
    }

    .irs-line {
        background: $progress-bg;
    }
}

.irs--square {

    .irs-from,
    .irs-to,
    .irs-single,
    .irs-bar {
        background-color: $progress-bg;
    }

    .irs-handle {
        border: 3px solid $primary;
        background-color: $progress-bg;
    }

    .irs-line {
        background: $progress-bg;
    }

    .irs-min,
    .irs-max {
        top: 0;
        padding: 1px 3px;
        color: $gray-700;
        font-size: 10px;
        line-height: 1.333;
        text-shadow: none;
        background-color: $progress-bg;
        border-radius: 4px;
    }
}